import LinkedinPost from "./LinkedinPost"
import Posts from './messages.json'
import SocialProfile from './socialprofile.json'
import './Feed.css'
import { useEffect, useState } from "react"
import axios from "axios";
import ArrowRight from './assets/arrow_right.svg'

const accessToken = 'MVjiJycEp_2UAAYAB974a3ICEv7mj1Yp7qbwbwbOCUw.6LUsfmGYy6Cv5dI3fA-K8yS9y5V6CyC-bDGF34pYzvI';

function Feed() {
  const [posts, setPosts] = useState(null);
  const [profile, setProfile] = useState(null);

  useEffect(() => {
    if (window.self !== window.top) {
      if (window.self !== window.top) {
        // Page is loaded inside an iframe
        console.log("Page is loaded within an iframe.");
      } else {
        // Page is not loaded inside an iframe (top-level window)
        console.log("Page is not loaded within an iframe.");
      }
    }

    setPosts(Posts.data);
    setProfile(SocialProfile.data)
  }, []);

  return (    
    <div className="feed">
      <h4>LinkedIn</h4>
      {posts && posts.map(post => (
        <>
          <LinkedinPost key={post.id} post={post} profile={profile} />
        </>
      ))}
      <div className="cta">
        <a href="" className="link">View LinkedIn page <span className="icon-right"><img src={ArrowRight} /></span></a>
      </div>
    </div>
  )
}

export default Feed