import Calendar from 'react-calendar';
import './CalendarReact.css'

function CalendarReact({ handleDayClick, customEvents }) {

  const tileClassName = ({ date }) => {
    const formattedDate = new Date(date).toDateString();
    return customEvents.some((event) => event.date === formattedDate) ? 'event-day' : null;
  };

  return (
    <div>
      <Calendar onClickDay={handleDayClick} tileClassName={tileClassName} />
    </div>
  )
}

export default CalendarReact
